    margin: 0;
    padding: 0;
    gap: 0;
    font-family: sans-serif;
    font-size: 16px;
}



/*      dialog      */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh; 
    border: none;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal.active{
    display: flex;
    
}

.details_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    max-height: 60vh;
    max-width: 60vw;
    min-height: 30vh;
    min-width: 30vw;
    background-color: rgb(226, 223, 223);
    border-radius: 5px;
    padding: 10px;
}

.detailsGrp{
    display: flex;
    text-align: left;

}

.details_descriptionValue{
   
    word-wrap: break-word;
}

.details_container div{
    width: 25rem;
}

.details_container span{
    text-align: left;
    width: 9rem;
}
.detailsGrp :first-child{
    font-size: 1.3rem;
    font-weight: bold;
    color: #484849;
}

.details_title{
    font-size: 1.7rem;
    margin-bottom: 10px;
    color: #484849;
    font-weight: bold;
    
    
}

.details_container span{
    font-size: 1.2rem;
    color: #484849;
  
}

::placeholder{
    font-weight: 2rem;
}

form{
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 5rem 1fr;
    height: 60vh;
    width: 60vw;
    min-height: 40vh;
    min-width: 40vw;
 }


 .create_project{
    cursor: pointer;
    font-size: 1.3rem;
 }

.add_content{
    display: flex;
    flex-direction: column;
    padding: 20px 30px;
    background-color: rgb(243, 243, 243);
    justify-content: space-between;
}

input,textarea{
    border: none;
    height: 3rem;
    padding-left: 10px;
    background-color:transparent ;
}

#date{
    cursor: pointer;
}

#date::-webkit-calendar-picker-indicator{
    cursor: pointer;
}

input[type=radio]{
   display: none;
}

.prio_container{
    display: flex;
    gap: 20px;
    text-align: center;
    align-items: center;
}

#prio_low,
#prio_medium,
#prio_high{
    transition: background-color 0.5s;
    transition: color 0.2s;
}

#prio_low{
    border: 1px solid green;
    color: green;


    
}

#low:checked + label,#prio_low:hover{
    background-color: green;
    color: white; 
 }

#prio_medium{
    border: 1px solid orange;
    color: orange;
    
}

#medium:checked + label,#prio_medium:hover{
    background-color: orange;
    color: white; 
 }

#prio_high{
    border: 1px solid red;
    color: red;
}

#high:checked + label,#prio_high:hover{
    background-color: red;
    color: white; 
 }


.prio_container label{
    display: inline-block;
    cursor: pointer;
    border: 1px solid black;
    padding: 5px 10px;

}

.prio_wrapper button{
    border: 1px solid green;
    color: rgb(2, 168, 2);
    padding: 5px 10px;
    cursor: pointer;
    transition: background-color 0.5s;
    transition: color 0.2s;
}

.prio_wrapper button:hover{
    background-color: green;
    color: white; 
}


.prio_wrapper{
    display: flex;
    justify-content: space-between;
}

textarea{
    height: 250px;
}



form .header{
    display: flex;
    grid-column: 1/3;
    background-color: rgb(56, 55, 53);
    color: white;
    padding-left: 2rem;
    align-items: center;
    
}

.header h2{
    font-size: 2rem;
    font-weight: 900;
}

.sidebar{
    background-color: rgb(226, 224, 224);
    padding: 50px 40px 0 40px;
    
}

/* main content */

.container{
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: 8rem 1fr;
    justify-content: center;
    
}

.container .header{
    display: flex;
    grid-column: 1/3;
    color: white;
    background-color: rgb(56, 55, 53);
    align-items: center;
    font-weight: 900;
    font-size: 4rem;
    padding-left: 5rem;
}


.content{
    display: flex;
    justify-content: center;
    background-color: rgb(243, 243, 243);
}

.main{
    display: flex;
    flex-direction: column;
    
    width: 40vw;
    padding: 30px 100px 30px 100px;
    gap: 20px;
}

.todo{
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    padding: 5px 30px;
    background-color: rgb(228, 231, 233);
    height: 50px;
    
}

.todo_delete,.editBtn{
    display: flex;
    text-align: center;
    align-items: center;
    cursor: pointer;
    width: 50px;
    height: 50px;
}

.detailsBtn{
    height: 3vh;
    width: 4vw;
    background-color: transparent;
    cursor: pointer;
    border: 1px solid rgb(4, 196, 4);
    color:rgb(4, 163, 4);
    transition: all .3s
}

.detailsBtn:hover{
    background-color: rgb(4, 163, 4);
    color: white;
}

i:hover{
    color: #02bd3a; 
 }


.left_panel,.right_panel{
    display: flex;
    align-items: center;
    gap: 40px;
}

.priority-low{
    border-left: 4px solid green;
}
.priority-medium{
    border-left: 4px solid orange;
}
.priority-high{
    border-left: 4px solid red;
}

/*      side panel      */

.container .sidebar{
    display: flex;
    flex-direction: column;
    padding: 50px;
    font-size: 1.5rem;
    gap: 4rem;
    background-color: rgb(226, 224, 224);
}

.nav,.sidebar_folders{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.sidebar_title{
    font-size: 2rem;
}


.sidebar button{
    display: inline-block;
    background-color: transparent;
    border: none;
    text-align: center;
    font-size: 20px;
    width: 10rem;
    height: 2rem;
    cursor: pointer;
}

.sidebar button:hover{
    background-color: rgb(208, 211, 211);
}

.home,.today,.week,.sidebar_container{
    margin-left: 1.5rem;
    font-size: 1.3rem;
}

.folder{
    cursor: pointer;
}

.selected_folder,.selected{
    background-color: rgb(198, 204, 204);
}

.delete_folder{
    cursor: pointer;
    width: 10px;
}

.sidebar_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

li{
    list-style: none;
    cursor: pointer;
    width: 180px;
    font-size: 1.3rem;
}

.delete_folder{
    display: none;
}

.sidebar_container:hover .delete_folder,
.selected_folder .delete_folder{
    display: inline-block;
}

.sidebar_container:hover{
    background-color: rgb(198, 204, 204);
}


.add_todo{
    display: flex;
    align-items: center;
    padding-left: 30px;
    background-color: transparent;
    text-align: left;
    height: 50px;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    gap: 8px;
}

.add_todo:hover{
    background-color: rgb(228, 231, 233);
}

.add_todo span{
    font-weight: 900;
    font-size: 1.5rem;
    margin-bottom: 3px;
}

.fileName{
    font-size: 2.5rem;
    color: rgb(105, 105, 105);
    
}

#submitBtn{
    border: 1px solid green;
    color: rgb(2, 168, 2);
    padding: 5px 10px;
    cursor: pointer;
    transition: all 0.5s
}

#submitBtn:hover{
    background-color: green;
    color: white; 
}

.checkbox{
    height: 50px;
    color: white;
    background-color: #02bd3a;
}



.todo:has(.checkbox:checked)::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background: gray;
    transform: translateY(-50%);
    opacity: 0.6;
}

.todo:has(.checkbox:checked){
    color: gray;
    opacity: 0.6;
}

.editBtn{
    width: 20px;
    height: 20px;
}

.sidebar,button{
    user-select: none; 
    -moz-user-select: none; 
    -webkit-text-select: none; 
    -webkit-user-select: none; 
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,MAAM;IACN,uBAAuB;IACvB,eAAe;AACnB;;;;AAIA,qBAAqB;AACrB;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,oCAAoC;IACpC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;AACjB;;AAEA;IACI,aAAa;;AAEjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,gBAAgB;IAChB,eAAe;IACf,gBAAgB;IAChB,eAAe;IACf,oCAAoC;IACpC,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,gBAAgB;;AAEpB;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,WAAW;AACf;AACA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;IACd,iBAAiB;;;AAGrB;;AAEA;IACI,iBAAiB;IACjB,cAAc;;AAElB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;IAC5B,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,eAAe;CAClB;;;CAGA;IACG,eAAe;IACf,iBAAiB;CACpB;;AAED;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,oCAAoC;IACpC,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,6BAA6B;AACjC;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;GACG,aAAa;AAChB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;;;IAGI,iCAAiC;IACjC,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;IACvB,YAAY;;;;AAIhB;;AAEA;IACI,uBAAuB;IACvB,YAAY;CACf;;AAED;IACI,wBAAwB;IACxB,aAAa;;AAEjB;;AAEA;IACI,wBAAwB;IACxB,YAAY;CACf;;AAED;IACI,qBAAqB;IACrB,UAAU;AACd;;AAEA;IACI,qBAAqB;IACrB,YAAY;CACf;;;AAGD;IACI,qBAAqB;IACrB,eAAe;IACf,uBAAuB;IACvB,iBAAiB;;AAErB;;AAEA;IACI,uBAAuB;IACvB,qBAAqB;IACrB,iBAAiB;IACjB,eAAe;IACf,iCAAiC;IACjC,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;IACvB,YAAY;AAChB;;;AAGA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;AACjB;;;;AAIA;IACI,aAAa;IACb,gBAAgB;IAChB,iCAAiC;IACjC,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;;AAEvB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,oCAAoC;IACpC,yBAAyB;;AAE7B;;AAEA,iBAAiB;;AAEjB;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;IAC5B,uBAAuB;;AAE3B;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,YAAY;IACZ,iCAAiC;IACjC,mBAAmB;IACnB,gBAAgB;IAChB,eAAe;IACf,kBAAkB;AACtB;;;AAGA;IACI,aAA